@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-300.woff2"), url("../fonts/Montserrat-300.woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat.woff2"), url("../fonts/Montserrat.woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/Montserrat-Bold.woff2"), url("../fonts/Montserrat-Bold.woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  font-family: inherit;
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

button,
input {
  outline: none;
  border: none;
}
button:focus,
input:focus {
  outline: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  width: 100%;
  max-width: 450px;
  padding: 18px;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.02em;
  color: #ffffff;
  background-color: #55c1ff;
  border-radius: 5px;
  cursor: pointer;
}
.btn:hover {
  opacity: 0.8;
}

.highlight-text {
  color: #55c1ff;
}

.page {
  height: 100%;
  min-width: 320px;
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 1;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #ffffff;
}

.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  background-color: #141414;
}

.box {
  max-width: 1600px;
  width: calc(100% - 332px);
  margin: 0 auto;
}
@media (max-width: 1600px) {
  .box {
    width: calc(100% - 80px);
  }
}
@media (max-width: 767px) {
  .box {
    width: calc(100% - 40px);
  }
}

.form__group {
  display: flex;
  max-width: 640px;
}
@media (max-width: 1024px) {
  .form__group {
    flex-direction: column;
  }
}

.form__input {
  padding: 16px 30px;
  width: 100%;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.02em;
  color: #262626;
  background-color: #ffffff;
  border-radius: 5px;
}
.form__input::placeholder {
  color: rgba(38, 38, 38, 0.3);
}

.form__btn {
  width: 100%;
  max-width: 450px;
  padding: 18px;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.02em;
  color: #ffffff;
  background-color: #55c1ff;
  border-radius: 5px;
  cursor: pointer;
}
.form__btn:hover {
  opacity: 0.8;
}

.hero {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
  min-height: 47vw;
}
@media (max-width: 767px) {
  .hero {
    height: auto;
    max-height: unset;
  }
}
@media (min-width: 768px) {
  .hero::before {
    background: rgba(0, 0, 0, 0.6);
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    bottom: 0;
    width: 50%;
    height: 100%;
    z-index: -1;
    backdrop-filter: blur(10px);
  }
}

.hero__img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
  z-index: -2;
}

