.experience {
  position: relative;
}

.experience__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: -1;
}
.experience__bg::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(20, 20, 20, 0) 0%, #141414 79.57%);
  backdrop-filter: blur(10px);
}
@media (max-width: 767px) {
  .experience__bg::before {
    background: linear-gradient(180deg, rgba(20, 20, 20, 0) 0%, #141414 29.57%);
  }
}

.experience__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 86px 0;
}
@media (max-width: 1024px) {
  .experience__content {
    padding: 40px 0;
  }
}
@media (max-width: 767px) {
  .experience__content {
    flex-direction: column;
  }
}

.experience__title {
  max-width: 1110px;
  margin: 0 auto;
  padding-top: 212px;
  font-weight: bold;
  font-size: 72px;
  line-height: 75px;
  text-align: center;
}
@media (max-width: 1024px) {
  .experience__title {
    padding-top: 120px;
    font-size: 36px;
    line-height: 37px;
  }
}
@media (max-width: 767px) {
  .experience__title {
    font-size: 48px;
    line-height: 50px;
  }
}

.experience__wrap {
  width: 100%;
}
@media (min-width: 768px) {
  .experience__wrap--small {
    width: 33%;
  }
}
@media (min-width: 768px) {
  .experience__wrap--wide {
    width: calc(67% - 40px);
  }
}
@media (min-width: 1439px) {
  .experience__wrap--wide {
    width: calc(67% - 70px);
  }
}
@media (max-width: 767px) {
  .experience__wrap--wide {
    order: -1;
  }
}

.experience__item {
  border-radius: 15px;
  overflow: hidden;
}
.experience__item:not(:last-child) {
  margin-bottom: 50px;
}
@media (max-width: 767px) {
  .experience__item--wide {
    margin: 0 -20px;
    margin-bottom: 60px;
    border-radius: 0;
