.select select {
  background: none;
  border: 0;
  appearance: none;
  font-family: inherit;
  width: 100%;
  height: 100%;
  padding: 15px 0;
  padding-right: 5px;
  display: block;
  outline: none;
  cursor: pointer;
}
.select select::-ms-expand {
  display: none;
}
.select::after {
  content: "";
  display: block;
  width: 8px;
  height: 8px;
  border-bottom: 2px solid #000000;
  border-right: 2px solid #000000;
  transform: rotate(45deg) translateY(-50%);
  position: absolute;
  right: 20px;
  top: 50%;
  pointer-events: none;
}

